@import url(https://fonts.googleapis.com/css?family=Poppins&display=swap);
* {
  margin: 0;
  padding: 0;
}

.no-match{
  font-size: 32px;
  text-align: center;
  
}

.no-match a:hover{
font-size: 1000px;
-webkit-transition: all ease 10s;
transition: all ease 10s;
}

/* Scrollbars */

::-webkit-scrollbar {
  height: 5px;
  width: 5px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 2px grey; 
  border-radius: 10px;
}
 
/* Handle */
::-webkit-scrollbar-thumb {
  background: #666; 
  border-radius: 10px;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: #ddd; 
}

/* Errors n stuff */
